<template>
  <v-container>
    <v-layout
      text-xs-center
      wrap
    >
      <v-flex mb-4>
        <p>* 해당되지 않는 경우 -1표시. *</p>
        <h1>userAgent : <span></span></h1>
        <hr>
        <ul>
            <li>userAgent :         <span>{{userAgent}}</span></li>
            <li>isChrome :          <span>{{isChrome > -1 ? true:false}}</span></li>
            <li>isMobile :          <span>{{isMobile}}</span></li>
            <li>isSafari :          <span>{{isSafari}}</span></li>
            <li>isSamsungBrowser :  <span>{{isSamsungBrowser}}</span></li>
            <li>isWindows :         <span>{{isWindows}}</span></li>
            <li>isEdge :            <span>{{isEdge}}</span></li>
            <li>isIE :              <span>{{isIE}}</span></li>
        </ul>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import $ from 'jquery';
export default {
  data: () => ({
    userAgent : null,
    isChrome : null,
    isSafari : null,
    isMobile : null,
    isSamsungBrowser : null,
    isWindows : null,
    isEdge : null,
    isIE : null,
  }),
  computed: {
  },
  watch:{
  },
  created(){
    this.CheckOs();
    this.$store.dispatch('route','/10_connectOsCheck');
  },
  beforeDestroy(){
    $('.v-content__wrap').css('background-color', 'white');
  },
  methods:{
    CheckOs(){
      // includes는 IE에서 동작하지 않아서 indexOf를 사용
      var userAgent = window.navigator.userAgent;
      this.userAgent = userAgent;
      this.isChrome = userAgent.indexOf('Chrome');
      this.isSafari = userAgent.indexOf('Safari');
      this.isMobile = userAgent.indexOf('Mobile');
      this.isSamsungBrowser = userAgent.indexOf('SamsungBrowser');
      this.isWindows = userAgent.indexOf('Windows NT');
      this.isEdge = userAgent.indexOf('Edge');
      this.isIE = userAgent.indexOf('Trident');

      // 크롬 브라우저 체크
      if(this.isChrome > -1 || this.isChromeMobile > -1){
          if(this.isSamsungBrowser < 0 && this.isEdge < 0){
            $('.v-content__wrap').css('background-color', 'yellow');
          }
      }

      // for demo page
      // var checkList = [userAgent, this.isChrome, this.isChromeMobile, this.isSamsungBrowser, this.isWindows, this.isEdge, this.isIE];

      var result1 = "King" && "Arthur";
      console.log(result1); //Arthur
      var result2 = "Arthur" && "King";
      console.log(result2); // King

      // $('h1 span').text(checkList[0]);

      // checkList.forEach(function(item, index) {   
      //   if(item > -1){
      //         item = true;
      //         $('li:nth-child('+index+') span').text(item);
      //         $('li:nth-child('+index+')').css({'background': '#193491', 'color': '#fff'});
      //     }
      //     else {
      //         item = false;
      //         $('li:nth-child('+index+') span').text(item);
      //     }
      // });
    }
  }
}

</script>
<style>
h1, h2, li{
  margin-top: 4px;
  padding: 4px 8px;
  font-size: 20px;
}
h1, h2, li > span{
  font-weight: 700;
}
h1{ 
  font-size:  16px;
}
p{
  margin: 20px 0;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
}
</style>